body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(black,blue,black);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.footerClass{
    display:grid;
    resize: both;
    grid-template-columns: 60% 40%;
    grid-template-rows: auto;
    height: auto;
    
}



@media screen and (max-width: 768px) {
  .footerClass {
    display:grid;
    resize: both;
    grid-template-columns: 100%;
    grid-template-rows: auto;
    height: auto;
  }
}
.Home{
    height: 90vh;
    background:transparent;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    padding-top: 25%;

}
.mainImage{
    font-size:150px;
    color:rgb(224, 148, 148);
}
@media screen and (max-width: 768px) {
    .Home{
        height: 200vh;
    }
}

    .headerClass{
        display:grid;
        resize: both;
        grid-template-columns: 60% 40%;
        height: auto;
    }
    .leftSpace{
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;        
        background:transparent!important;
    }
    
    .Logo{
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        color:white;
        font-weight: bold;
        font-size: 25px;
        padding-left:10px;
        padding-top:10px;
    }
    .rightSpace{
        display:flex;
        flex-direction: row;
        justify-content: space-around;
        background:transparent!important;
    }
    a{
        display: flex;
        flex-direction: row;
        text-decoration: none;
        justify-content: center;
        align-items: center;    
        opacity: 0.7;
        color:white;
    }
    a:hover{
        opacity: 1;
        color:yellow;
    }
    p{
        display: flex;
        flex-direction: row;
        text-decoration: none;
        justify-content: center;
        align-items: center;    
        opacity: 0.7;
        color:white;
    }
    p:hover{
        opacity: 1;
        color:yellow;
    }    


      

 @media screen and (max-width: 420px) {
    .headerClass{
        display:flex;
        flex-direction: column;        
        height: auto;
    }
    
    .rightSpace{        
        flex-direction: row;        
        justify-content: space-around;
    }
    .leftSpace{        
        flex-direction: column;        
        justify-content: space-around;
    }
    .linkStyle{
        flex-direction: column;
    }
    a{
        flex-direction: column;
    }
    p{
        flex-direction: column;
    }
        a:hover{
        opacity: 1;
        color:yellow;
    }    
    p:hover{
        opacity: 1;
        color:yellow;
    }    
}
.About{
    height: 90vh;    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background: linear-gradient(black,rgb(112, 112, 3),blue);
    padding-top:10%;
}

.innerAbout{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 70%;
    color:white!important;
    font-weight: bold;
}
.pAbout{
    color:white!important;    
}
@media screen and (max-width: 420px) {
    .About{
        height: 200vh;
    }
}

.Shop{
    height: 90vh;    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background: linear-gradient(black,orange,blue);
    padding-top:10%
}
.ItemsArray{
    display:flex;
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    margin: 10px;
}
@media screen and (max-width: 420px) {
    .Shop{
        height: 200vh;
    }
}
.item{
    width:  200px;
    background: rgb(243, 229, 185);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 10px;
    border-radius: 10px;
}
.imageComponent{
    display: flex;
    flex-direction: column;
    width: 50%;
    height: auto;
    border-radius:10px ;
    margin-bottom: 10px;
}
.addButton{
    width: 25%;
    height: 35px;
    border-radius: 10px;
    background: transparent;
    text-align: center;
    font-weight: bold;
    font-size: 18px;
    padding-top: 10px;
    margin-bottom:10px;
    margin-left: 10px;
}
.addButton:hover{        
    background: rgb(145, 136, 110);
}
.linePrice{
    display: flex;
    justify-content: space-between;
    width: 80%;
    flex-direction: row;
}
.priceLabel{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 18px;
}
.quantity{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    width: 20%;
    padding:5px;
    margin:5px;
    margin-top:0px;
    padding-top:5px;
}
.boxQuantity{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100px;
    margin-top: 10px;
    
}
.quantityButtons{
    display: flex;
    flex-direction: column;
    margin-right: 10px;
    
}
.QButtons{
    background: gold;
}
.quantityInput{
    display:flex;
    flex-direction: row;
    width: 50%!important;
    font-weight: bold;
    position:relative;    
    border-radius: 2px;
    padding:0;
    font-size: 25px;
}

.CartForm{
    height: 90vh;    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background: linear-gradient(black,rgb(180, 245, 245),blue);
}
.cartTitle{
    color:white;
}
.ticket{
    text-decoration: none;
}
.liTicket{    
    display: flex;
    flex-direction: row;
    list-style-type: none;
    width: 300px;
    align-items: center;
    justify-content: space-around;
    font-weight: bold;
    border-style: none none solid none;
    text-align: center;
}
.liTicketHeader{    
    display: flex;
    flex-direction: row;
    list-style-type: none;
    width: 300px;
    align-items: center;
    justify-content: space-around;
    font-weight: bold;
    border-style: none none double none;
    margin-bottom: 20px;
    color:red;
    text-align: center;
}
.ulTicket{
    text-decoration: none;
}
.itemName{
    display:flex;
    flex-direction: row;    
    width: 30%;
}
.price{
    display: flex;
    flex-direction: row;
    margin-left: 0;
    width: 30%;
}

.totalLine{
    display: flex;
    flex-direction: row;   
    justify-content: space-between; 
    width: 300px;
    font-size: 25px;
    font-weight: bold;
}
.finishButton{
    display:flex;
    background: gold;
    width: auto;
    height: auto;
    margin-top: 30px;
    font-size: 20px;
    border-radius: 5px;
    box-shadow: 0px 0px 15px 0px black ;
}
.trash{
    background: transparent;
    border-style: none;
}
.trash:hover{
    font-size: 20px;
}
@media screen and (max-width: 420px) {
    .CartForm{
        height: 200vh;
    }
}
